:root {
  --font-inter: "Inter", sans-serif;
}

:root {
  --limegreen: hsl(163, 72%, 41%);
  --brightred: hsl(356, 69%, 56%);
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instagram-start: hsl(37, 97%, 70%);
  --instagram-middle: hsl(5, 77%, 71%);
  --instagram-end: hsl(329, 70%, 58%);
  --instagram: linear-gradient(
    90deg,
    hsl(37, 97%, 70%),
    hsl(5, 77%, 71%),
    hsl(329, 70%, 58%)
  );
  --youtube: hsl(348, 97%, 39%);
  --toggle-bg-light: hsl(230, 22%, 74%);
  --toggle-bg-start: hsl(210, 78%, 56%);
  --toggle-bg-end: hsl(146, 68%, 55%);
  --toggle-light: hsl(230, 19%, 60%);
  --toggle-button-light: hsl(228, 46%, 96%);
  --dark-bg: hsl(230, 17%, 14%);
  --dark-top-bg: hsl(232, 19%, 15%);
  --dark-card: hsl(228, 28%, 20%);
  --dark-card-hover: hsl(228, 25%, 27%);
  --dark-text1: hsl(228, 34%, 66%);
  --dark-text2: hsl(0, 0%, 100%);
  --light-bg: hsl(0, 0%, 100%);
  --light-top-bg: hsl(225, 100%, 98%);
  --light-card: hsl(227, 47%, 96%);
  --light-card-hover: hsl(228, 33%, 91%);
  --light-text1: hsl(230, 12%, 44%);
  --light-text2: hsl(230, 17%, 14%);
  --background: var(--light-bg);
  --background-top: var(--light-top-bg);
  --text-color: var(--light-text2);
  --text-color2: var(--light-text1);
  --card-bg: var(--light-card);
  --card-hover: var(--light-card-hover);
  --toggle: var(--toggle-light);
  --toggle-bg: var(--toggle-bg-light);
  --toggle-button: var(--toggle-button-light);
  --social-media-border: var(--facebook);
}

@media (prefers-color-scheme: dark) {
  :root {
    --background: var(--dark-bg);
    --background-top: var(--dark-top-bg);
    --text-color: var(--dark-text2);
    --text-color2: var(--dark-text1);
    --card-bg: var(--dark-card);
    --card-hover: var(--dark-card-hover);
    --toggle: var(--light-bg);
    --toggle-bg: linear-gradient(
      225deg,
      var(--toggle-bg-end) 0%,
      var(--toggle-bg-start) 98.02%
    );
    --toggle-button: var(--dark-bg);
  }
}
body.light {
  --background: var(--light-bg);
  --text-color: var(--light-text2);
  --text-color2: var(--light-text1);
  --card-bg: var(--light-card);
  --card-hover: var(--light-card-hover);
  --toggle: var(--toggle-light);
  --toggle-bg: var(--toggle-bg-light);
  --toggle-button: var(--toggle-button-light);
}

body.dark {
  --background: var(--dark-bg);
  --background-top: var(--dark-top-bg);
  --text-color: var(--dark-text2);
  --text-color2: var(--dark-text1);
  --card-bg: var(--dark-card);
  --card-hover: var(--dark-card-hover);
  --toggle: var(--light-bg);
  --toggle-bg: linear-gradient(
    225deg,
    var(--toggle-bg-end) 0%,
    var(--toggle-bg-start) 98.02%
  );
  --toggle-button: var(--dark-bg);
}

html {
  font-size: 100%;
  box-sizing: border-box;
  font-family: var(--font-inter);
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  background-color: var(--background);
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
}

h1 {
  font-size: 1.5rem;
  margin-bottom: 0.375rem;
}
@media (min-width: 40em) {
  h1 {
    font-size: 1.75rem;
  }
}

h2 {
  font-size: 1.375rem;
  color: var(--text-color2);
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.container {
  max-width: 72.5rem;
  margin: 2.625rem auto 0 auto;
  padding: 0 1.5625rem;
}

.cards.negative-margin-top {
  margin-top: -5.8125rem;
}
@media (max-width: 39.9375em) {
  .cards.negative-margin-top {
    margin-top: -2.625rem;
  }
}

.header__background {
  background-color: var(--background-top);
  height: 15rem;
}

.header {
  margin-top: 0;
  padding-top: 2.625rem;
}
@media (min-width: 40em) {
  .header {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
}
.header__followers {
  font-weight: 700;
  color: var(--text-color2);
}
.header h1 {
  color: var(--text-color);
  letter-spacing: 0.2px;
}
.header__divider {
  color: var(--text-color2);
  opacity: 0.3;
  margin: 1.5625rem 0 1.25rem 0;
}
@media (min-width: 40em) {
  .header__divider {
    display: none;
  }
}

fieldset {
  border: none;
  padding: 0;
}

.toggle {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.toggle label {
  cursor: pointer;
  font-size: 0.75rem;
  color: var(--text-color2);
  font-weight: 700;
  margin-right: 0.625rem;
}
@media (max-width: 39.9375em) {
  .toggle label {
    margin-right: 0rem;
  }
}
.toggle__wrapper {
  position: relative;
  height: 1.5rem;
}
.toggle input[type=radio] {
  display: inline-block;
  width: 1.5rem;
  height: 1.5rem;
  margin: 0 -0.125rem 0 -0.125rem;
  opacity: 0;
}
.toggle__background {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  pointer-events: none;
  background: var(--toggle-bg);
  border-radius: 0.75rem;
}
.toggle__switcher {
  position: absolute;
  top: 0.125rem;
  left: 0.125rem;
  right: 100%;
  width: calc(50% - 0.140625rem);
  height: calc(100% - 0.25rem);
  border-radius: 50%;
  background-color: var(--toggle-button);
  transition: all 0.1s ease-out;
}
.toggle #light:checked ~ .toggle__switcher {
  right: 3px;
  left: calc(100% - 21px);
}

.cards {
  display: grid;
  gap: 2rem;
  margin: 2.625rem auto 3.25rem auto;
}
@media (min-width: 40em) {
  .cards {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 71.875em) {
  .cards {
    grid-template-columns: repeat(4, 1fr);
  }
}

.card {
  position: relative;
  background-color: var(--card-bg);
  padding: 2rem 0 1.5625rem 0;
  text-align: center;
  border-radius: 0.3125rem;
  overflow: hidden;
  cursor: pointer;
}
.card:hover {
  background-color: var(--card-hover);
}
.card::before {
  content: "";
  position: absolute;
  background: var(--social-media-border);
  width: 100%;
  height: 0.3125rem;
  left: 0;
  top: 0;
}
.card__twitter {
  --social-media-border:var(--twitter);
}
.card__instagram {
  --social-media-border:var(--instagram);
}
.card__youtube {
  --social-media-border:var(--youtube);
}
.card__platform {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 1.875rem;
}
.card__username {
  color: var(--text-color2);
  margin-left: 0.5625rem;
  font-size: 0.75rem;
  font-weight: 700;
}
.card__count {
  color: var(--text-color);
  font-size: 2.625rem;
  font-weight: 700;
}
.card__followers {
  color: var(--text-color2);
  text-transform: uppercase;
  font-size: 0.625rem;
  letter-spacing: 0.25rem;
  margin-bottom: 1.875rem;
}
.card__change {
  display: flex;
  justify-content: center;
  align-items: center;
}
.card__today {
  font-size: 0.75rem;
  font-weight: 700;
  margin-left: 0.3125rem;
}
.card__today--up {
  color: var(--limegreen);
}
.card__today--down {
  color: var(--brightred);
}

.card__small {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, auto);
  row-gap: 1.875rem;
  background-color: var(--card-bg);
  padding: 1.6875rem 1.9375rem 1.6875rem 1.5rem;
  cursor: pointer;
}
.card__small:hover {
  background-color: var(--card-hover);
}
.card__small__metric {
  font-size: 0.8125rem;
  font-weight: 700;
  color: var(--text-color2);
}
.card__small img {
  justify-self: end;
}
.card__small__count {
  font-size: 1.5625rem;
  font-weight: 700;
  color: var(--text-color);
}
.card__small .card__change {
  justify-self: end;
  align-self: end;
}